import React from 'react'

export const ProjectsCard = ({imageName, imageTitle, title, description, techStack, githubLink, deployedLink}) => {

  return (
    <div className="project-card">
      <img src={`Projects-Images/${imageName}`} alt={imageTitle} />
      <p className="project-title">{title}</p>
      <p className="project-description">{description}</p>
      <p className="project-tech-stack">{`Tech stack used(${techStack})`}</p>
      <a className="project-github-link" href={githubLink}>GitHub Link</a>
      <a className="project-deployed-link" href={deployedLink}>Deployed Link</a>
    </div>
  )
}
